.primary-btn{
  @include btn($orange);
  padding:10px 40px; 
  display: inline-block;
  transition: all .6s;
  position: relative;
  overflow: hidden;
  border: none;
  z-index: 1;

  &:after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -2;
}
&:before {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 0%;
    height: 100%;
    background-color: darken($blue-color, 10%);
    transition: all .3s;
    z-index: -1;
}
&:hover::before {
        width: 100%;
    }

}
.secondary-btn{
    @include btn($dark-blue);
    padding: 10px 40px;
    transition: all 0.2s ease-out;
    &:hover{
        background-color: transparent;
        color:$dark-blue;
        border: 2px solid $dark-blue;
    }
}
